import { Component, Input, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { Subject } from 'rxjs';
import { takeUntil, pluck, switchMap, map, tap } from 'rxjs/operators';
import { BlogListService } from '../blog-list.service';

@Component({
  selector: 'app-blog-list-pagination',
  templateUrl: './blog-list-pagination.component.html',
  styleUrls: ['./blog-list-pagination.component.scss']
})
export class BlogListPaginationComponent implements OnInit {

  protected destroy$ = new Subject();

  @Input() params: string;
  @Input() name: string;

  public pageSize = 10;
  public total: Array<number>;
  public correctPage: number;
  public link = [''];

  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private blogListService: BlogListService
  ) { }

  ngOnInit(): void {
    // if (!this.correctPage) {
    //   this.router.navigate([], { queryParams: { page: 1 } });
    // }

    this.activatedRoute.params
      .pipe(
        takeUntil(this.destroy$),
        pluck(this.params || ''),
        switchMap((params: string) => {
          return this.activatedRoute.queryParamMap.pipe(
            map((paramMap: ParamMap) => +paramMap.get('page') || 1),
            switchMap((page: number) => {
              return this.blogListService.blogAllList$.pipe(
                map((routes) => {
                  if (params) {
                    return routes.filter((route) => {
                      return (
                        route[this.name]?.[0] != null &&
                        route[this.name][0].toLowerCase() ===
                          params.toLowerCase()
                      );
                    });
                  } else {
                    return routes;
                  }
                }),
                tap((links) => {
                  const total = Math.ceil(links.length / this.pageSize);
                  this.total = Array(total)
                    .fill(total)
                    .map((e, i) => i);
                  this.correctPage = page;
                }),
                map((links) =>
                  links.slice((page - 1) * this.pageSize, page * this.pageSize)
                ),
              );
            })
          );
        })
      )
      .subscribe((res) => {
        this.blogListService.blogPageList$.next(res);
      });

  }
  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();

  }
}
